.partners {
  background: linear-gradient(103.42deg, #F39B6E 14.25%, #FCC975 64.12%, #FFF594 114.57%);
  padding-top: 110px;
  padding-bottom: 75px;

  &__list {
    @include list();
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, auto));
    grid-auto-rows: minmax(123px, auto);
    grid-column-gap: 40px;
    grid-row-gap: 10px;
    justify-items: center;
    align-items: center;
    background-color: #fff;
    padding: 0 60px;
  }

  &__item {
    min-height: 123px;
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  &__link {
    display: block;
  }
}

@media (max-width: $mobile-width) {
  .partners {
    padding-top: 55px;
    padding-bottom: 37px;

    &__list {
      grid-template-columns: repeat(1, minmax(120px, auto));
      grid-row-gap: 20px;
      grid-column-gap: 0;
    }
  }
}
