.adv-panel {
  background-color: rgba(255, 255, 255, 0.1);
  max-width: 282px;
  margin-left: auto;
  padding: 30px 15px;
  padding-bottom: 80px;
  text-align: center;
  color: #D3D3D3;
  font-size: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.25);
  position: relative;
  
  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 1.2;
    color: #fff;
  }
  
  &__input {
    width: 100%;
    margin-bottom: 10px;
  }
  
  &__button {
    width: 100%;
    margin-bottom: 20px;
  }
  
  &__timer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    min-height: 72px;
    background-color: #f5f5f5;
    
  }
  
  b {
    font-size: 14px;
    line-height: 1.2;
  }
  
  p {
    margin: 0;
    margin-bottom: 15px;
  }
}

@media (max-width: $mobile-width) {
  .adv-panel {
    max-width: 100vw;
  }
}