.steps-item {
  position: relative;
  padding: 25px 30px;
  background-color: $white;
  border: 10px solid transparent;
  background-clip: padding-box;
  border-radius: 20px;

  &__head {
    display: flex;
    align-items: center;
    margin: 0;
    color: #333;
    font-size: 20px;
    line-height: 1.2;
    font-weight: bold;
    margin-bottom: 20px;
  }

  &__num {
    flex-shrink: 0;
    font-family: "Muller", "Arial", sans-serif;
    display: inline-block;
    margin-right: 10px;
    background: linear-gradient(237.95deg, #FFF594 0%, #FCC975 66.35%, #F39B6E 96.39%), #CFD8DC;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 30px;
    line-height: 1.2;
    color: #FCC975;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
    font-size: 16px;
    color: #606060;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    margin: -10px;
    background: linear-gradient(237.95deg, #FFF594 0%, #FCC975 66.35%, #F39B6E 96.39%);
    border-radius: 20px;
    opacity: 0;
    transition: all .4s;
  }

  &:hover {
    &::before {
      opacity: 1;
    }
  }
}

@media (max-width: $mobile-width) {
  .steps-item {
    border: none;
    padding: 25px 0;
    
    &__head {
      font-size: 16px;
    }
    
    &::before {
      content: none;
    }
  }
}
