.nav-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  @include list();

  &__link {
    display: block;
    padding: 10px 30px;
    font-size: 14px;
    line-height: 1.2;
    color: $white;
    text-decoration: none;
    font-weight: normal;
    transition: all .4s;
    position: relative;

    &:hover,
    &:focus {
      opacity: .7;
    }
  }

  &--header {
    .nav-list__link {
      &::before {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #942164;
        transition: all .2s;
        transform: scale(0);
      }

      &:hover::before {
        transform: scale(1);
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .nav-list {
    &--header {
      .nav-list__link {
        padding: 10px 15px;

        &::before {
          content: none;
        }
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .nav-list {
    display: none;
    flex-direction: column;
    align-items: flex-start;
    position: fixed;
    top: 91px;
    left: 0;
    right: 0;
    height: 100vh;
    background-color: $black;
    z-index: 50;
    overflow-y: auto;

    &__link {
      font-size: 16px;
      padding: 15px;
    }
  }

  .nav-list.active {
    display: block !important;
  }
}
