.free {
  &__wrap {
    padding-bottom: 180px;
  }

  &__content {
    @include min-max(290px, 606px, 0);
    margin: 0;
    padding: 20px 0;
  }

  &__lead {
    padding: 20px 0;
    display: block;
    color: #333;
    position: relative;
  }

  &__achieve-item {
    max-width: 505px;
  }

  &__input {
    min-width: 280px;
  }

  &__button {
    min-width: 280px;
    margin-left: 35px;
  }

  &--car {
    &::before {
      content: "";
      width: 392px;
      height: 392px;
      background: linear-gradient(237.95deg, #FFF594 0%, #FCC975 66.35%, #F39B6E 96.39%);
      border-radius: 50%;
      position: absolute;
      top: -100px;
      right: -450px;
      z-index: -1;
    }

    &::after {
      content: "";
      display: none;
      width: 630px;
      height: 350px;
      background-image: url('../img/avto.png');
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      position: absolute;
      top: 0;
      right: -640px;
    }
  }
}

@media (max-width: 1352px) {
  .free {

    &--car {
      &::before {
        width: 300px;
        height: 300px;
        top: -100px;
        right: -400px;
      }

      &::after {
        width: 500px;
        top: -35px;
        right: -519px;
      }
    }
  }
}

@media (max-width: 1154px) {
  .free {

    &__content {
      max-width: 100%;
    }

    &__lead {
      padding-bottom: 300px;
    }

    &--car {
      &::before {
        width: 182px;
        height: 182px;
        top: 80px;
        right: 150px;
      }

      &::after {
        display: none;
        width: 430px;
        height: 250px;
        top: 80px;
        right: 100px;
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .free {
    &__wrap {
      padding-bottom: 40px;
    }

    &__lead {
      padding-bottom: 220px;
    }

    &__input {
      min-width: 100%;
      margin: 10px 0;
      font-size: 16px;
    }

    &__button {
      min-width: 100%;
      margin: 10px 0;
    }

    &--flex-wrap {
      flex-direction: column;
      width: 100%;
    }

    &--car {
      &::before {
        width: 130px;
        height: 130px;
        top: 80px;
        right: 65px;
      }

      &::after {
        display: none;
        width: 300px;
        height: 200px;
        top: 80px;
        right: 0px;
      }
    }
  }
}
