.script-block {
  margin-left: auto;
  max-width: 860px;
  position: relative;
  z-index: 2;

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: bold;
    font-size: 28px;
    line-height: 1.2;
  }

  &__subtitle {
    margin: 0;
    margin-top: 10px;
    font-size: 18px;
    line-height: 1.2;
    font-weight: normal;
  }

  &__top {
    padding: 50px 100px;
    padding-bottom: 20px;
    color: #fff;
    background: linear-gradient(179.94deg, #CE1273 0.09%, #942164 28.04%, #7b235b 69.27%);
  }

  &__bottom {
    padding: 50px 100px;
    padding-top: 120px;
    color: #000;
    background: linear-gradient(180deg, #F39B6E 0%, #FCC975 51.04%, #FFF594 100%);
    position: relative;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      height: 100px;
      background-image: url("../img/figure-red-1.png");
      background-repeat: no-repeat;
      background-position: top center;
      background-size: contain;
    }
  }

  &__input {
    min-width: 280px;
    margin-right: 35px;
    border-color: #3C3C3C;
    background-color: #8B8B8B;

    &:hover::placeholder,
    &:focus::placeholder {
      color: #fff;
    }

    &:hover,
    &:focus {
      background-color: #3C3C3C;
      color: #fff;
    }
  }

  &__video-btn {
    flex-shrink: 0;
    margin-right: 20px;
  }

  &__text-cont {
    color: #fff;
    font: inherit;

    p {
      margin: 0;
      margin-bottom: 8px;
    }
    
    span {
      font-size: 14px;
    }

    b {
      font-size: 16px;
      display: block;
      margin-bottom: 10px;
    }

    li {
      line-height: 1.7;
    }

    .little {
      font-size: 14px;
    }

    span {
      display: block;
      line-height: 1.4;
    }

    ol {
      padding: 0;
      padding-left: 17px;
    }

    &--black {
      font-size: 14px;
      color: #333333;
    }
  }

  &--woman {
    &::before {
      content: '';
      position: absolute;
      bottom: -50px;
      left: -460px;
      width: 500px;
      height: 500px;
      background-image: url("../img/circle-1.svg");
      border-radius: 50%;
      z-index: 1;
    }

    &::after {
      content: "";
      width: 500px;
      height: 115%;
      position: absolute;
      bottom: -175px;
      left: -430px;
      background-image: url("../img/woman.png");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: contain;
      z-index: 2;
    }
  }

  &--man {
    &::before {
      content: '';
      position: absolute;
      top: 100px;
      left: -550px;
      width: 630px;
      height: 630px;
      background-image: url("../img/circle-2.svg");
      border-radius: 50%;
      z-index: -1;
    }

    &::after {
      content: "";
      width: 470px;
      height: 575px;
      position: absolute;
      top: 60px;
      left: -450px;
      background-image: url("../img/man.png");
      background-repeat: no-repeat;
      background-position: center bottom;
      background-size: contain;
      border-bottom-left-radius: 35%;
      border-bottom-right-radius: 40%;
      overflow: hidden;
    }
  }
}

@media (max-width: $tablet-width) {
  .script-block {
    max-width: 100vw;
    margin: 0 -15px;

    &--man,
    &--woman {

      &::before,
      &::after {
        content: none;
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .script-block {

    &__title {
      font-size: 21px;
    }

    &__top {
      padding: 25px;
      padding-bottom: 10px;
    }

    &__bottom {
      padding: 25px;
      padding-top: 60px;
    }

    &--flex-wrap {
      flex-direction: column;
      width: 100%;
    }

    &__input {
      min-width: calc(100vw - 30px);
      margin: 10px 0;
    }

    &__button {
      width: calc(100vw - 30px);
    }
  }
}
