.lead {
  padding: 40px 0;
  background-color: $black;
  color: $white;
  overflow: hidden;

  &__subwrap {
    position: relative;
    background-image: url("../img/text.png"), url("../img/lead-back.jpg");
    background-repeat: no-repeat;
    background-position: top left, top right -20px;
    background-size: 630px, 750px;
    min-height: calc(100vh - 165px);
    z-index: 2;
  }

  &__video-btn {
    position: absolute;
    top: 300px;
    left: 360px;

    &::after {
      content: "";
      width: 205px;
      height: 117px;
      background-image: url("../img/dec-lines.svg");
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      top: 30%;
      left: -100px;
    }

    span {
      color: #E8E8E8;
      position: absolute;
      top: 110px;
      left: -180px;
      text-decoration: underline;
      white-space: nowrap;
      transform: rotate(-16deg);
      transition: all .4s;

      &:hover {
        opacity: .6;
      }
    }
  }
}

@media (max-width: $tablet-width) {
  .lead {
    &__subwrap {
      background-position: top left, bottom right 10px;
      background-size: 430px, 550px;
      min-height: 600px;
    }

    &__video-btn {
      top: 359px;
      left: 120px;

      span {
        top: -25px;
        left: -3px;
        transform: rotate(0);
      }
    }
  }
}

@media (max-width: $mobile-width) {
  .lead {
    &__subwrap {
      padding-top: 400px;
      background-position: top left, top 100px right -70px;
      background-size: 330px, 300px;
      min-height: 600px;
    }

    &__video-btn {
      top: 230px;
      left: 20px;

      span {
        top: -25px;
        left: -3px;
        transform: rotate(0);
      }
    }
  }
}
