.features-item {
  padding-top: 143px;
  text-align: center;
  position: relative;

  &__img-wrapper {
    width: 140px;
    height: 143px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .svg-radio {
    height: 115px;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .svg-tech {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  .svg-inst {
    position: absolute;
    width: 106px;
    height: 34px;
    right: -3%;
    bottom: 45%;
  }

  .svg-vk {
    position: absolute;
    width: 32px;
    height: 42px;
    top: 8%;
    left: 0%;
  }

  .svg-movie {
    position: absolute;
    top: 19%;
    left: 18.5%;
    width: 88px;
    height: 40px;
  }

  .svg-movie-main {
    width: 141px;
    bottom: 0;
    left: 50%;
  }

  .svg-book {
    width: 112px;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
  }

  .svg-loop {
    width: 45px;
    height: 45px;
    position: absolute;
    top: 22%;
    left: 13%;
  }

  .svg-pen {
    width: 67px;
    height: 67px;
    position: absolute;
    bottom: 24%;
    right: 5%;

  }

  &__title {
    font-size: 20px;
    font-weight: bold;
    line-height: 1.4;
    color: #404040;
  }

  &__text {
    line-height: 1.5;
  }

  &:hover {
    .antenna {
      opacity: 1;
    }

    .svg-vk {
      animation: 1s moveup ease infinite;
    }

    .svg-inst {
      animation: 1s movedown ease infinite;
    }

    .svg-movie {
      animation: 1s rotate-movie ease infinite;
    }

    .svg-loop {
      animation: 1s rotate-movie ease infinite;
    }

    .svg-pen {
      animation: 1s pen ease infinite;
    }
  }
}

@media (max-width: $mobile-width) {
  .features-item {
    &__title {
      font-size: 16px;
    }

    &__img-wrapper {
      height: 155px;
    }
  }
}
