@mixin title() {
  font-size: 30px;
  line-height: 30px;
  text-transform: uppercase;
}

@mixin list() {
  padding: 0;
  margin: 0;
  list-style: none;
}

@mixin min-max($min-width, $max-width, $padding) {
  min-width: $min-width;
  max-width: $max-width;
  margin: 0 auto;
  padding: 0 $padding;
  box-sizing: border-box;
}
