.info {
  padding-top: 290px;
  padding-bottom: 160px;
  background: url("../img/figure-black.png"), linear-gradient(180deg, #FFF594 0%, #FCC975 44.28%, #F39B6E 89.06%);
  background-repeat: no-repeat, no-repeat;
  background-position: top center;
  background-size: 105%;
}

@media (max-width: $tablet-width) {
  .info {
    padding-top: 145px;
    padding-bottom: 80px;
    background-size: 100%;
  }
}

@media (max-width: $mobile-width) {
  .info {
    padding-top: 90px;
    padding-bottom: 40px;
  }
}
