.achieve-item {
  padding: 10px 0;
  display: flex;
  align-items: center;
  font-size: 14px;

  &__img-wrapper {
    flex-shrink: 0;
    width: 51px;
    height: 51px;
    overflow: hidden;
    margin-right: 15px;
  }

  &__text {
    margin: 0;
    color: #606060;

    &--white {
      color: #E8E8E8;
    }
  }
  
  &--less {
    font-size: 12px;
    
    .achieve-item__img-wrapper {
      width: 30px;
      height: 30px;
    }
  }
}
