.button {
  display: inline-block;
  padding: 8px 35px;
  border: none;
  border-radius: 3px;
  font-size: 12px;
  line-height: 1.2;
  color: #fff;
  text-decoration: none;
  background: linear-gradient(9.97deg, #612353 -1.83%, #942164 32.32%, #CE1273 66.46%);
  background-size: 200%;
  background-position: center right;
  cursor: pointer;
  outline: none;

  &--yellow {
    background: linear-gradient(15.56deg, #F39B6E 41.44%, #FCC975 76.03%, #FFF594 96.45%) center right;
    background-size: 200%;
    color: #000;
  }

  &:hover,
  &:focus {
    animation: .5s gradient ease;
    animation-fill-mode: both;
  }
}

.video-btn {
  position: relative;
  display: block;
  width: 120px;
  height: 72px;
  border-radius: 5px;
  background: linear-gradient(237.95deg, #FFF594 0%, #FCC975 66.35%, #F39B6E 96.39%);
  background-size: 200%;
  background-position: center right;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

  &::before {
    content: "";
    width: 40px;
    height: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-image: url("../img/icon-video.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &:hover,
  &:focus {
    animation: .5s gradient ease;
    animation-fill-mode: both;
  }
}
