.main-footer {
  padding-top: 55px;
  padding-bottom: 70px;
  background-color: #464646;
  font-size: 14px;
  color: #D4D4D4;

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  &__left {
    flex-shrink: 0;
  }

  &__right {
    display: flex;
    align-items: flex-start;
  }

  &__logo {
    margin-right: 115px;
    margin-bottom: 20px;
  }

  &__input {
    width: 320px;
    margin-right: 7px;
  }

  &__text {
    margin: 0;
    margin-bottom: 10px;
  }

  &__nav-list {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .nav-list__link {
      color: #D4D4D4;
    }
  }
}

@media (max-width: $tablet-width) {
  .main-footer {
    &__right {
      display: none;
    }

    &__wrap {
      padding: 0 15px;
    }
  }
}

@media (max-width: $mobile-width) {
  .main-footer {
    padding-top: 27px;
    padding-bottom: 35px;

  }
}
