*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}


body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #404040;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

img {
  max-width: 100%;
  height: auto;
}

.container {
  @include min-max(320px, 100%, 0);
}

.wrap {
  @include min-max(320px, 1570px, 15px);
  position: relative;
}

.subwrap {
  @include min-max(290px, 1170px, 15px);
}

.text-left {
  text-align: left !important;
}

.text-center {
  text-align: center;
  line-height: 1.4;
}

.gap {
  height: 80px;
}

.gap-2 {
  height: 50px;
}

.gap-3 {
  height: 30px;
}

.gap-4 {
  height: 10px;
}

@media (max-width: $tablet-width) {
  .wrap {
    @include min-max(320px, 1570px, 0);
    position: relative;
  }

  .subwrap {
    @include min-max(290px, 1170px, 15px);
  }
}

@media (max-width: $mobile-width) {
  body {
    font-size: 13px;
  }

  .gap {
    height: 20px;
  }
}
