.modal-video {
  display: none;
}

.modal-video.fancybox-content {
  padding: 40px;
  background-color: transparent;
}

.modal-video .fancybox-close-small {
  color: #fff;
}

.modal-main {
  min-width: 300px;
  max-width: 580px;
  display: none;
  text-align: center;
  color: #333;
  font-size: 14px;
  line-height: 1.2;

  &__content {
    padding: 10px 45px;
    background-color: #eceff1;
  }

  &__title {
    background-color: #fff;
    font-size: 20px;
    font-weight: normal;
    line-height: 1.2;
    margin: 0;
    padding: 20px 45px;
  }

  &__textarea {
    width: 100%;
    height: 108px;
    padding: 20px 25px;
    border: none;
    border-radius: 20px;
    outline: none;
    resize: none;
  }

  &__input {
    width: 280px;
  }

  &__flex-wrap {
    margin: 20px 0;
    justify-content: space-between;
  }

  &--success {
    .modal-main__content {
      padding-top: 100px;
      position: relative;

      &::before {
        content: "";
        width: 48px;
        height: 48px;
        background-color: transparent;
        border: 1px solid #942164;
        border-radius: 50%;
        position: absolute;
        top: 30px;
        left: calc(50% - 24px);
      }

      &::after {
        content: "";
        width: 25px;
        height: 50px;
        background-color: transparent;
        border: 1px solid #942164;
        border-left-color: transparent;
        border-top-color: transparent;
        position: absolute;
        top: 15px;
        left: calc(50% - 5px);
        transform: rotate(45deg);
      }
    }
  }
}

.modal-main.fancybox-content {
  padding: 0;
}

.modal-main .fancybox-button {
  width: 60px;
  height: 60px;
}

@media (max-width: $mobile-width) {
  .modal-video.fancybox-content {
    padding: 35px 0;
  }
  
  .modal-video .fancybox-close-small {
    right: -10px;
  }
}
