.title {
  margin: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  background: linear-gradient(90.4deg, #612353 0%, #942164 62.28%, #CE1273 100.89%), #000000;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 32px;
  font-weight: 900;
  line-height: 1.5;
  color: #CE1273;
  display: block;
  text-align: center;
  text-transform: uppercase;
}

@media (max-width: $tablet-width) {
  .title {
    font-size: 24px;
  }
}

@media (max-width: $mobile-width) {
  .title {
    font-size: 18px;
  }
}
