.no-scroll {
  position: fixed;
  left: 0;
  right: 0;
}

.slick-prev {
  position: absolute;
  top: 50%;
  left: 0px;
  width: 50px;
  height: 160px;
  z-index: 3;
  cursor: pointer;

  &:before {
    content: '';
    position: absolute;
    top: calc(50% - 10px);
    left: 0;
    width: 35px;
    height: 40px;
    background-image: url("../img/arrow-right.svg");
    background-position: top center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.slick-next {
  top: 50%;
  right: 0px;
  width: 50px;
  height: 160px;
  z-index: 3;
  cursor: pointer;

  &:before {
    content: '';
    position: absolute;
    top: calc(50% - 10px);
    right: 0;
    width: 35px;
    height: 40px;
    background-image: url("../img/arrow-left.svg");
    background-position: top center;
    background-repeat: no-repeat;
    background-size: contain;
  }
}

@media (max-width: $tablet-width) {
  .slick-prev {
    left: 10px;
  }
  
  .slick-next {
    right: 10px;
  }
}