.animation-show::after {
  display: block;
  animation: 1s car cubic-bezier(1, 0.01, 0.67, 1.04);
}

.bubles {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: 1;
}

.bubles-left {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 300px;
  z-index: 3;
}

.bubles-right {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 300px;
  z-index: 3;
}

.bubles-btm {
  z-index: 3;
  
  .ball {
    z-index: 3;
  }
}

.dust {
  background-image: url("../img/lead-dust.png");
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 100vh;

}

.ball {
  position: absolute;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-position: center;
}

.ball-o {
  width: 65px;
  height: 65px;
  background-color: $orange;
  background-image: url("../img/icon-odn.svg");
}

