.steps {
  padding: 100px 0;

  &__list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(340px, auto));
    grid-column-gap: 40px;
    grid-row-gap: 20px;
  }
}

@media (max-width: $mobile-width) {
  .steps {
    padding: 50px 0;
    grid-template-columns: repeat(1, minmax(290px, auto));
    grid-column-gap: 0px;

    &__list {
      grid-template-columns: repeat(1, minmax(290px, auto));
      grid-column-gap: 0;
    }
  }
}
