.burger {
  display: none;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

@media (max-width: $mobile-width) {
  .burger {
    display: block;
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
  }

  .burger-btn {
    display: block;
    background-color: $white;
    border: none;
    width: 100%;
    height: 2px;
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    outline: none;

    &::after,
    &::before {
      content: '';
      position: absolute;
      left: 0;
      background-color: $white;
      width: 100%;
      height: 2px;
      border-radius: 3px;
      transition: all .5s;
    }

    &::after {
      top: 16px;
    }

    &::before {
      top: 32px;
    }
  }

  .burger.active .burger-btn {
    background-color: rgba(0, 0, 0, 0);
  }

  .burger.active .burger-btn::after {
    transform: rotate(45deg);
  }

  .burger.active .burger-btn::before {
    transform: rotate(-45deg);
    top: 16px;
  }
}
