.video-wrap {
  position: relative;
  width: 80vw;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
}

.video-wrap iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

@media (max-width: $mobile-width) {
  .video-wrap {
    width: 90vw;
  }
}
