.main-header-wp {
  min-height: 91px;
}

.main-header {
  background-color: #000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  box-shadow: 0 2px 3px #191616;

  &__wrap {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px 15px;
  }

  &__button {
    margin-left: auto;
  }

  &__logo {
    margin-right: 20px;
  }
}

@media (max-width: $tablet-width) {
  .main-header {
    &__button {
      display: none;
    }
  }
}
