.project-input {
  padding: 7px 25px;
  font: inherit;
  font-size: 12px;
  color: #000;
  box-shadow: none;
  border: 1px solid #CFD8DC;
  border-radius: 3px;
  background-color: #ECEFF1;
  outline: none;
  transition: all .4s;
  
  &::placeholder {
    color: #000;
  }
  
  &:hover,
  &:focus {
    background-color: #CFD8DC;
  }
}

@media (max-width: $tablet-width) {
  .project-input {
    font-size: 16px;
  }
}
