.antenna {
  position: absolute;
  width: 30px;
  height: 30px;
  top: 0;
  left: 11%;
  opacity: 0;
  transition: opacity .5s;
}

.signal-1-container {
  position: absolute;
  left: 55%;
  top: 20px;
  width: 40px;
  height: 10px;
  overflow: hidden;
}

.signal-1 {
  position: absolute;
  top: 28%;
  width: 70%;
  height: 100%;
  border-radius: 50px 50px 0 0;
  border: 2px solid #fff;
  animation: 1s scale ease infinite;
}

.signal-2-container {
  position: absolute;
  left: 75%;
  top: 10px;
  width: 40px;
  height: 10px;
  overflow: hidden;
  transform: scale(2);
}

.signal-2 {
  position: absolute;
  top: 38%;
  width: 70%;
  height: 100%;
  border-radius: 50px 50px 0 0;
  border: 1px solid #fff;
  animation: 1s scale ease infinite;
}

.signal-3-container {
  position: absolute;
  left: 88%;
  top: 0px;
  width: 40px;
  height: 22px;
  overflow: hidden;
  transform: scale(3);
}

.signal-3 {
  position: absolute;
  top: 38%;
  width: 70%;
  height: 100%;
  border-radius: 50px 50px 0 0;
  border: .4px solid #fff;
  animation: 1s scale ease infinite;
}