.rewards-item {
  padding: 10px;
  outline: none;
  text-align: center;
  font-size: 20px;

  &__img-wrapper {
    width: 250px;
    height: 250px;
    overflow: hidden;
    margin-bottom: 20px;
    position: relative;

    &::before {
      content: "";
      width: 114px;
      height: 114px;
      position: absolute;
      top: 105px;
      right: 40px;
      border-radius: 50%;
      background-color: red;
      z-index: -1;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  &__year {
    color: #333333;
  }

  &__name {
    color: #942164;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    max-height: 63px;
    -webkit-line-clamp: 1;
    text-overflow: ellipsis;
  }

  &__deskr {
    font-size: 16px;
    color: #606060;
  }
}

.rewards-item:nth-child(odd) .rewards-item__img-wrapper::before {
  background: linear-gradient(180deg, #CE1273 0%, #942164 53.13%, #612353 100%);
}

.rewards-item:nth-child(even) .rewards-item__img-wrapper::before {
  background: linear-gradient(180deg, #FFF594 0%, #FCC975 51.56%, #F39B6E 100%);
}

@media (max-width: $mobile-width) {
  .rewards-item {
    font-size: 15px;
    
    &__deskr {
      font-size: 12px;
    }
    
    &__img-wrapper {
      margin: 0 auto;
      margin-bottom: 20px;
    }
  }
}
